<template>
    <div class="bgColor">
        <div class="header">
            <img src="../../../images/successPay.png" alt="">
            <div class="curatorText">
                <img src="../../../images/curatorText04.png" alt="">
                <!-- <p class="mask-top">请登陆后及时修改您的密码！</p> -->
            </div>
        </div>

        <div class="content">

            <p class="button-list">
                <span class="rechange" @click="goBack">前往大厅</span>
                <span class="insure" @click="loadAdmin">登录后台</span>
            </p>
        </div>
        <footer-text></footer-text>
    </div>

</template>

<script>
import footerText from '../../../components/footerText'
export default {
    components: {
        footerText,
  },
  data() {
      return {
          totalNumber:100,
      }
  },
  methods:{
      goBack:function(){
          this.$router.push({path:'/becomeCurator/paymentMethods'})
          //返回后台数据方法待添加
      },
      loadAdmin:function(){

      }

  }
}
</script>

<style lang="less" scoped>
    .bgColor{
        height:100%;
        min-height: 16rem;
        background:#EEEEEE;
        position: relative;
        #footer_text{
            position: absolute;
            bottom:0;
        }
    }
    .header{
        height: 7rem;
        background: #4CACFF;
        text-align: center;
        position: relative;
        img{
            width:84%;
            margin-top:14%;
        }
        .curatorText{
            position: absolute;
            width:84%;
            bottom:-21%;
            left:8%;
            margin:0 auto;
            text-align: center;
            img{
                width:100%;
                margin: 0;
            }
            // .mask-top{
            //     width:100%;
            //     height:1.3rem;
            //     line-height:1.3rem;
            //     font-size:.6rem;
            //     font-weight: bolder;
            //     color:#EE143D;
            //     position: absolute;
            //     bottom:0.3em;
            //     background: url(../../../images/mask01.png) no-repeat ;
            //     background-size: 100% 100%;
            // }
        }
    }
    .content{
        text-align: center;
        margin-top: 24%;


        .button-list{
             width:90%;
             margin: 0 auto;
        }
         .rechange, .insure{
             width:45%;
             height:1.4rem;
             color:#fff;
             font-size:.5rem;
             line-height: 1.4rem;
             margin: 0 1%;
             display: inline-block;
             background-color: #EE8F45;
             border-radius: 6px;
        }
        .insure{
            background: #5BA9ED;
        }

    }
    .text{
        text-align:center;
        color:#A2A2A2;
    }

    .bottom{
        width:90%;
        position: absolute;
        bottom:2%;
        left:5%;
        color:#AEAEAE;
        span{
            display:inline-block;
            text-align: center;
            font-size:.3rem;
            width:50%;
            float:left;
        }
    }
</style>



